/* eslint-disable react/prefer-stateless-function */
import React, { useContext, useEffect } from 'react';
import uuidv4 from 'PlugAndPlay/_global/js/uuid';
import { watchModals } from '@react-aria/aria-modal-polyfill';
import { OverlayProvider } from '@react-aria/overlays';

import Popover from '../../popover/jsx/popover';
import UserDetailsContext from './data/UserDetailsContext';
import UserDetailsContent from './UserDetailsContent';

/**
 * User Details
 */
export default function UserDetails({ popover, studentDataUrlPrefix }) {
    watchModals();

    const titleId = uuidv4();
    const { userDetails, getUserDetails, hasError } =
        useContext(UserDetailsContext);

    useEffect(() => {
        getUserDetails();
    }, []);

    const { firstName, lastName, lmsStudentid } = userDetails;

    const content = (
        <UserDetailsContent
            firstName={firstName}
            lastName={lastName}
            lmsStudentid={lmsStudentid}
            studentDataUrlPrefix={studentDataUrlPrefix}
            hasError={hasError}
        />
    );

    return (
        <>
            <OverlayProvider className="no-wysiwyg users-wrapper">
                {popover && (
                    <Popover
                        titleId={titleId}
                        trigger={({ props, ref }) => {
                            return (
                                <button
                                    ref={ref}
                                    {...props}
                                    type="button"
                                    className="users-wrapper__action"
                                >
                                    <svg className="svg-icon">
                                        <title>View user details</title>
                                        <use href="#user-avatar" />
                                    </svg>
                                    <div className="users-wrapper__name">
                                        {firstName} {lastName}
                                    </div>
                                </button>
                            );
                        }}
                        className="users-popover"
                    >
                        {content}
                    </Popover>
                )}
                {!popover && content}
            </OverlayProvider>
        </>
    );
}
