import React from 'react';
import { student } from 'PlugAndPlay/edify/js/getStudentMemoized';

export default function UserDetailsContent({
    closeModal,
    firstName,
    lastName,
    lmsStudentid,
    studentDataUrlPrefix,
    hasError,
}) {
    function logoutUser() {
        // Get all the items from local storage
        const localStorageKeys = Object.keys(localStorage);
        // For each of the keys delete the item
        localStorageKeys.forEach((item) => {
            localStorage.removeItem(item);
        });

        // Get all the items from session storage
        const sessionStorageKeys = Object.keys(sessionStorage);
        // For each of the keys delete the item
        sessionStorageKeys.forEach((item) => {
            sessionStorage.removeItem(item);
        });

        // Clear the memoized student
        student.clearMemoization();

        // Reload the current page
        // eslint-disable-next-line no-restricted-globals
        location.reload();
    }

    if (hasError) {
        return (
            <div className="user">
                <div className="user-details">
                    <p>
                        Looks like something has gone wrong. Please try
                        reloading the page
                    </p>
                    <button
                        className="user-details__list-item-button"
                        type="button"
                        onClick={logoutUser}
                    >
                        Logout
                    </button>
                </div>
            </div>
        );
    }

    return (
        <div className="user">
            <div className="user-details">
                <div className="user-details__header">
                    {firstName} {lastName}
                    <button
                        className="user-details__header-button"
                        type="button"
                        onClick={closeModal}
                    >
                        <span className="sr-only">Close</span>
                        <svg className="user-details__header-button-icon">
                            <use href="#close" />
                        </svg>
                    </button>
                </div>
                <ul className="user-details__list">
                    <li className="user-details__list-item">
                        <a
                            className="user-details__list-item-link"
                            href={`${studentDataUrlPrefix}${lmsStudentid}`}
                        >
                            View profile information
                        </a>
                    </li>
                    <li className="user-details__list-item">
                        <button
                            className="user-details__list-item-button"
                            type="button"
                            onClick={logoutUser}
                        >
                            Logout
                        </button>
                    </li>
                </ul>
            </div>
        </div>
    );
}
